// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import { Can } from '@src/utility/context/Can.js'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Form,
  Badge,
  Label,
  Input,
  Button,
  CardBody,
  FormFeedback,
  Modal,
  ModalHeader,
  ModalBody,
  Table
} from 'reactstrap'
import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import { getData, addDepartment, updateDepartment, deleteDepartment } from './store/department'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Third Party Components
import classnames from 'classnames'
import { useDispatch, useSelector } from 'react-redux'
import { Edit, Eye, Plus, Trash } from 'react-feather'

const Department = ({ }) => {
  const MySwal = withReactContent(Swal)
  const [data, setData] = useState([])
  const [selected, setSelected] = useState(null)
  const [formModal, setFormModal] = useState(false)
  const dispatch = useDispatch()
  const store = useSelector(state => state.department)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const schema = yup.object().shape({
    organizationId: yup.string().default(activeOrgId),
    name: yup.string().required('Please Enter Department'),
    updatedBy: yup.string().default(userId),
    createdBy: yup.string().default(userId)
  })

  const { handleSubmit, formState: { errors }, control, reset } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const deletefn = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteDepartment(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Department has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        await dispatch(getData())
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  const resetForm = async () => {
    reset({
      organizationId: activeOrgId,
      id: null,
      name: ''
    })
    setFormModal(false)
    setSelected(null)
  }

  const onSubmit = async data => {
    setBlock(true)
    if (selected !== null) {
      await dispatch(updateDepartment(data))
      resetForm()
      setSelected(null)
    } else {
      await dispatch(addDepartment(data))
      resetForm()
    }
    await dispatch(getData())
    setBlock(false)
  }

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  useEffect(async () => {
    await dispatch(getData())
  }, [])

  const openEditModal = card => {
    setFormModal(true)
    setSelected(card)
    reset({
      organizationId: activeOrgId,
      id: card.id,
      name: card.name
    })
  }

  useEffect(() => {
    setData(store.data)
  }, [store.data])

  return (
    <Fragment>
      <Card>
        <CardBody className='py-25'>
          <Row className='gx-4'>
            <Row className='mb-50'>
              <Col>
                <Button color='primary' size='sm' onClick={() => { setFormModal(true) }}> <Plus size={15} />Add</Button>
              </Col>
            </Row>
            <Col lg='12' className='mt-2 mt-lg-0'>
              <Table className='table table-bordered table-hover'>
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Status</th>
                    <th>No of Users</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {data.map((card, index) => {
                    return (
                      <tr key={index}>

                        <td className='mb-0'>{card.name}</td>
                        <td>
                          {card.status === 1 ? (
                            <Badge color='light-success' className='ms-50'>
                              Active
                            </Badge>
                          ) : (
                            <Badge color='light-danger' className='ms-50'>
                              InActive
                            </Badge>
                          )}
                        </td>
                        <td>{card.noofusers}</td>
                        <td>
                          <Can I={'write'} a={'role'}>
                            <div className='d-flex '>
                              <Eye size={15} className='me-1' />
                              {!card.isdefault &&
                                <div className='d-flex'>
                                  <Edit size={15} className='me-75' onClick={() => openEditModal(card)}>
                                    Edit
                                  </Edit>
                                  <Trash size={15} onClick={() => { deletefn(card.id) }}>Delete</Trash>
                                </div>
                              }
                            </div>
                          </Can>
                        </td>
                      </tr>
                    )
                  })}
                </tbody>
              </Table>
            </Col>

            <Modal
              isOpen={formModal}
              toggle={() => setFormModal(!formModal)}
              className='modal-dialog-centered modal-lg'
            >
              <UILoader blocking={block} loader={<Loader />}>
                <ModalHeader className='bg-transparent' toggle={() => setFormModal(!formModal)}>
                  Department
                </ModalHeader>
                <ModalBody>
                  <Col>
                    {
                      Object.keys(errors).map((key) => {

                        return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

                      })
                    }
                    <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>

                      {getRow('Department Name', 'name')}

                      <Col className='mt-2 pt-1' xs={12}>
                        <Button type='submit' className='me-1' color='primary'>
                          Submit
                        </Button>
                        <Button type='button' color='warning' onClick={resetForm}>
                          Cancel
                        </Button>
                      </Col>

                    </Row>
                  </Col>
                </ModalBody>
              </UILoader>
            </Modal>
          </Row>
        </CardBody>
      </Card>

    </Fragment>
  )
}

export default Department
